			 <div class="content"> 
       <p>社会信用体系是社会主义市场经济体制和社会治理体制的重要组成部分。它以法律、法规、标准和契约为依据，以健全覆盖社会成员的信用记录和信用基础设施网络为基础，以信用信息合规应用和信用服务体系为支撑，以树立诚信文化理念、弘扬诚信传统美德为内在要求，以守信激励和失信约束为奖惩机制，目的是提高全社会的诚信意识和信用水平。<br />
	<!--<br />-->
	　　加快社会信用体系建设是全面落实科学发展观、构建社会主义和谐社会的重要基础，是完善社会主义市场经济体制、加强和创新社会治理的重要手段，对增强社会成员诚信意识，营造优良信用环境，提升国家整体竞争力，促进社会发展与文明进步具有重要意义。<br />
	<!--<br />-->       
	       &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;加强政务诚信、商务诚信、社会诚信和司法公信建设&rdquo;，党的十八届三中全会提出的&ldquo;建立健全社会征信体系，褒扬诚信，惩戒失信&rdquo;，《中共中央 国务院关于加强和创新社会管理的意见》提出的&ldquo;建立健全社会诚信制度&rdquo;，以及《中华人民共和国国民经济和社会发展第十二个五年规划纲要》(以下简称&ldquo;十二五&rdquo;规划纲要)提出的&ldquo;加快社会信用体系建设&rdquo;的总体要求，制定本规划纲要。规划期为20142020年。<br />
	一、社会信用体系建设总体思路<br />
	
	　　(一)发展现状。
	<br />
	　　党中央、国务院高度重视社会信用体系建设。有关地区、部门和单位探索推进，社会信用体系建设取得积极进展。国务院建立社会信用体系建设部际联席会议制度统筹推进信用体系建设，公布实施《征信业管理条例》，一批信用体系建设的规章和标准相继出台。全国集中统一的金融信用信息基础数据库建成，小微企业和农村信用体系建设积极推进；各部门推动信用信息公开，开展行业信用评价，实施信用分类监管；各行业积极开展诚信宣传教育和诚信自律活动；各地区探索建立综合性信用信息共享平台，促进本地区各部门、各单位的信用信息整合应用；社会对信用服务产品的需求日益上升，信用服务市场规模不断扩大。<br />
	<!--<br />-->
	　　我国社会信用体系建设虽然取得一定进展，但与经济发展水平和社会发展阶段不匹配、不协调、不适应的矛盾仍然突出。存在的主要问题包括：覆盖全社会的征信系统尚未形成，社会成员信用记录严重缺失，守信激励和失信惩戒机制尚不健全，守信激励不足，失信成本偏低；信用服务市场不发达，服务体系不成熟，服务行为不规范，服务机构公信力不足，信用信息主体权益保护机制缺失；社会诚信意识和信用水平偏低，履约践诺、诚实守信的社会氛围尚未形成，重特大生产安全事故、食品药品安全事件时有发生，商业欺诈、制假售假、偷逃骗税、虚报冒领、学术不端等现象屡禁不止，政务诚信度、司法公信度离人民群众的期待还有一定差距等。<br />
	<!--<br />-->
	　　(二)形势和要求。
	<br />
	　　我国正处于深化经济体制改革和完善社会主义市场经济体制的攻坚期。现代市场经济是信用经济，建立健全社会信用体系，是整顿和规范市场经济秩序、改善市场信用环境、降低交易成本、防范经济风险的重要举措，是减少政府对经济的行政干预、完善社会主义市场经济体制的迫切要求。<br />
	<!--<br />-->
	　　我国正处于加快转变发展方式、实现科学发展的战略机遇期。加快推进社会信用体系建设，是促进资源优化配置、扩大内需、促进产业结构优化升级的重要前提，是完善科学发展机制的迫切要求。<br />
	<!--<br />-->
	　　我国正处于经济社会转型的关键期。利益主体更加多元化，各种社会矛盾凸显，社会组织形式及管理方式也在发生深刻变化。全面推进社会信用体系建设，是增强社会诚信、促进社会互信、减少社会矛盾的有效手段，是加强和创新社会治理、构建社会主义和谐社会的迫切要求。<br />
	<!--<br />-->
	　　我国正处于在更大范围、更宽领域、更深层次上提高开放型经济水平的拓展期。经济全球化使我国对外开放程度不断提高，与其他国家和地区的经济社会交流更加密切。完善社会信用体系，是深化国际合作与交往，树立国际品牌和声誉，降低对外交易成本，提升国家软实力和国际影响力的必要条件，是推动建立客观、公正、合理、平衡的国际信用评级体系，适应全球化新形势，驾驭全球化新格局的迫切要求。<br />
	<!--<br />-->
	　　(三)指导思想和目标原则。<br />
	<!--<br />-->
	　　全面推动社会信用体系建设，必须坚持以邓小平理论、&ldquo;三个代表&rdquo;重要思想、科学发展观为指导，按照党的十八大、十八届三中全会和&ldquo;十二五&rdquo;规划纲要精神，以健全信用法律法规和标准体系、形成覆盖全社会的征信系统为基础，以推进政务诚信、商务诚信、社会诚信和司法公信建设为主要内容，以推进诚信文化建设、建立守信激励和失信惩戒机制为重点，以推进行业信用建设、地方信用建设和信用服务市场发展为支撑，以提高全社会诚信意识和信用水平、改善经济社会运行环境为目的，以人为本，在全社会广泛形成守信光荣、失信可耻的浓厚氛围，使诚实守信成为全民的自觉行为规范。<br />
	<!--<br />-->
	　　社会信用体系建设的主要目标是：到2020年，社会信用基础性法律法规和标准体系基本建立，以信用信息资源共享为基础的覆盖全社会的征信系统基本建成，信用监管体制基本健全，信用服务市场体系比较完善，守信激励和失信惩戒机制全面发挥作用。政务诚信、商务诚信、社会诚信和司法公信建设取得明显进展，市惩社会满意度大幅提高。全社会诚信意识普遍增强，经济社会发展信用环境明显改善，经济社会秩序显著好转。<br />
	<!--<br />-->
	　　社会信用体系建设的主要原则是：<br />
	<!--<br />-->
	　　政府推动，社会共建。充分发挥政府的组织、引导、推动和示范作用。政府负责制定实施发展规划，健全法规和标准，培育和监管信用服务市常注重发挥市锄制作用，协调并优化资源配置，鼓励和调动社会力量，广泛参与，共同推进，形成社会信用体系建设合力。<br />
	<!--<br />-->
	　　健全法制，规范发展。逐步建立健全信用法律法规体系和信用标准体系，加强信用信息管理，规范信用服务体系发展，维护信用信息安全和信息主体权益。<br />
	<!--<br />-->
	　　统筹规划，分步实施。针对社会信用体系建设的长期性、系统性和复杂性，强化顶层设计，立足当前，着眼长远，统筹全局，系统规划，有计划、分步骤地组织实施。<br />
	<!--<br />-->
	　　重点突破，强化应用。选择重点领域和典型地区开展信用建设示范。积极推广信用产品的社会化应用，促进信用信息互联互通、协同共享，健全社会信用奖惩联动机制，营造诚实、自律、守信、互信的社会信用环境。<br />
	<!--<br />-->
	　　二、推进重点领域诚信建设<br />
	<!--<br />-->
	　　(一)加快推进政务诚信建设。<br />
	<!--<br />-->
	　　政务诚信是社会信用体系建设的关键，各类政务行为主体的诚信水平，对其他社会主体诚信建设发挥着重要的表率和导向作用。<br />
	<!--<br />-->
	　　坚持依法行政。将依法行政贯穿于决策、执行、监督和服务的全过程，全面推进政务公开，在保护国家信息安全、商业秘密和个人隐私的前提下，依法公开在行政管理中掌握的信用信息，建立有效的信息共享机制。切实提高政府工作效率和服务水平，转变政府职能。健全权力运行制约和监督体系，确保决策权、执行权、监督权既相互制约又相互协调。完善政府决策机制和程序，提高决策透明度。进一步推广重大决策事项公示和听证制度，拓宽公众参与政府决策的渠道，加强对权力运行的社会监督和约束，提升政府公信力，树立政府公开、公平、清廉的诚信形象。<br />
	<!--<br />-->
	　　发挥政府诚信建设示范作用。各级人民政府首先要加强自身诚信建设，以政府的诚信施政，带动全社会诚信意识的树立和诚信水平的提高。在行政许可、政府采购、招标投标、劳动就业、社会保障、科研管理、干部选拔任用和管理监督、申请政府资金支持等领域，率先使用信用信息和信用产品，培育信用服务市场发展。<br />
	<!--<br />-->
	　　加快政府守信践诺机制建设。严格履行政府向社会作出的承诺，把政务履约和守诺服务纳入政府绩效评价体系，把发展规划和政府工作报告关于经济社会发展目标落实情况以及为百姓办实事的践诺情况作为评价政府诚信水平的重要内容，推动各地区、各部门逐步建立健全政务和行政承诺考核制度。各级人民政府对依法作出的政策承诺和签订的各类合同要认真履约和兑现。要积极营造公平竞争、统一高效的市撤境，不得施行地方保护主义措施，如滥用行政权力封锁市尝包庇纵容行政区域内社会主体的违法违规和失信行为等。要支持统计部门依法统计、真实统计。政府举债要依法依规、规模适度、风险可控、程序透明。政府收支必须强化预算约束，提高透明度。加强和完善群众监督和舆论监督机制。完善政务诚信约束和问责机制。各级人民政府要自觉接受本级人大的法律监督和政协的民主监督。加大监察、审计等部门对行政行为的监督和审计力度。<br />
	<!--<br />-->
	　　加强公务员诚信管理和教育。建立公务员诚信档案，依法依规将公务员个人有关事项报告、廉政记录、年度考核结果、相关违法违纪违约行为等信用信息纳入档案，将公务员诚信记录作为干部考核、任用和奖惩的重要依据。深入开展公务员诚信、守法和道德教育，加强法律知识和信用知识学习，编制公务员诚信手册，增强公务员法律和诚信意识，建立一支守法守信、高效廉洁的公务员队伍。<br />
	<!--<br />-->
	　　(二)深入推进商务诚信建设。<br />
	<!--<br />-->
	　　提高商务诚信水平是社会信用体系建设的重点，是商务关系有效维护、商务运行成本有效降低、营商环境有效改善的基本条件，是各类商务主体可持续发展的生存之本，也是各类经济活动高效开展的基础保障。<br />
	<!--<br />-->
	　　生产领域信用建设。建立安全生产信用公告制度，完善安全生产承诺和安全生产不良信用记录及安全生产失信行为惩戒制度。以煤矿、非煤矿山、危险化学品、烟花爆竹、特种设备生产企业以及民用爆炸物品生产、销售企业和爆破企业或单位为重点，健全安全生产准入和退出信用审核机制，促进企业落实安全生产主体责任。以食品、药品、日用消费品、农产品和农业投入品为重点，加强各类生产经营主体生产和加工环节的信用管理，建立产品质量信用信息异地和部门间共享制度。推动建立质量信用征信系统，加快完善12365产品质量投诉举报咨询服务平台，建立质量诚信报告、失信黑名单披露、市场禁入和退出制度。<br />
	<!--<br />-->
	　　流通领域信用建设。研究制定商贸流通领域企业信用信息征集共享制度，完善商贸流通企业信用评价基本规则和指标体系。推进批发零售、商贸物流、住宿餐饮及居民服务行业信用建设，开展企业信用分类管理。完善零售商与供应商信用合作模式。强化反垄断与反不正当竞争执法，加大对市踌淆行为、虚假宣传、商业欺诈、商业诋毁、商业贿赂等违法行为的查处力度，对典型案件、重大案件予以曝光，增加企业失信成本，促进诚信经营和公平竞争。逐步建立以商品条形码等标识为基础的全国商品流通追溯体系。加强检验检疫质量诚信体系建设。支持商贸服务企业信用融资，发展商业保理，规范预付消费行为。鼓励企业扩大信用销售，促进个人信用消费。推进对外经济贸易信用建设，进一步加强对外贸易、对外援助、对外投资合作等领域的信用信息管理、信用风险监测预警和企业信用等级分类管理。借助电子口岸管理平台，建立完善进出口企业信用评价体系、信用分类管理和联合监管制度。<br />
	<!--<br />-->
	　　金融领域信用建设。创新金融信用产品，改善金融服务，维护金融消费者个人信息安全，保护金融消费者合法权益。加大对金融欺诈、恶意逃废银行债务、内幕交易、制售假保单、骗保骗赔、披露虚假信息、非法集资、逃套骗汇等金融失信行为的惩戒力度，规范金融市场秩序。加强金融信用信息基础设施建设，进一步扩大信用记录的覆盖面，强化金融业对守信者的激励作用和对失信者的约束作用。<br />
	<!--<br />-->
	　　税务领域信用建设。建立跨部门信用信息共享机制。开展纳税人基础信息、各类交易信息、财产保有和转让信息以及纳税记录等涉税信息的交换、比对和应用工作。进一步完善纳税信用等级评定和发布制度，加强税务领域信用分类管理，发挥信用评定差异对纳税人的奖惩作用。建立税收违法黑名单制度。推进纳税信用与其他社会信用联动管理，提升纳税人税法遵从度。<br />
	<!--<br />-->
	　　价格领域信用建设。指导企业和经营者加强价格自律，规范和引导经营者价格行为，实行经营者明码标价和收费公示制度，着力推行&ldquo;明码实价&rdquo;。督促经营者加强内部价格管理，根据经营者条件建立健全内部价格管理制度。完善经营者价格诚信制度，做好信息披露工作，推动实施奖惩制度。强化价格执法检查与反垄断执法，依法查处捏造和散布涨价信息、价格欺诈、价格垄断等价格失信行为，对典型案例予以公开曝光，规范市场价格秩序。<br />
	<!--<br />-->
	　　工程建设领域信用建设。推进工程建设市场信用体系建设。加快工程建设市场信用法规制度建设，制定工程建设市场各方主体和从业人员信用标准。推进工程建设领域项目信息公开和诚信体系建设，依托政府网站，全面设立项目信息和信用信息公开共享专栏，集中公开工程建设项目信息和信用信息，推动建设全国性的综合检索平台，实现工程建设项目信息和信用信息公开共享的&ldquo;一站式&rdquo;综合检索服务。深入开展工程质量诚信建设。完善工程建设市场准入退出制度，加大对发生重大工程质量、安全责任事故或有其他重大失信行为的企业及负有责任的从业人员的惩戒力度。建立企业和从业人员信用评价结果与资质审批、执业资格注册、资质资格取消等审批审核事项的关联管理机制。建立科学、有效的建设领域从业人员信用评价机制和失信责任追溯制度，将肢解发包、转包、违法分包、拖欠工程款和农民工工资等列入失信责任追究范围。<br />
	<!--<br />-->
	　　政府采购领域信用建设。加强政府采购信用管理，强化联动惩戒，保护政府采购当事人的合法权益。制定供应商、评审专家、政府采购代理机构以及相关从业人员的信用记录标准。依法建立政府采购供应商不良行为记录名单，对列入不良行为记录名单的供应商，在一定期限内禁止参加政府采购活动。完善政府采购市场的准入和退出机制，充分利用工商、税务、金融、检察等其他部门提供的信用信息，加强对政府采购当事人和相关人员的信用管理。加快建设全国统一的政府采购管理交易系统，提高政府采购活动透明度，实现信用信息的统一发布和共享。<br />
	<!--<br />-->
	　　招标投标领域信用建设。扩大招标投标信用信息公开和共享范围，建立涵盖招标投标情况的信用评价指标和评价标准体系，健全招标投标信用信息公开和共享制度。进一步贯彻落实招标投标违法行为记录公告制度，推动完善奖惩联动机制。依托电子招标投标系统及其公共服务平台，实现招标投标和合同履行等信用信息的互联互通、实时交换和整合共享。鼓励市场主体运用基本信用信息和第三方信用评价结果，并将其作为投标人资格审查、评标、定标和合同签订的重要依据。<br />
	<!--<br />-->
	　　交通运输领域信用建设。形成部门规章制度和地方性法规、地方政府规章相结合的交通运输信用法规体系。完善信用考核标准，实施分类考核监管。针对公路、铁路、水路、民航、管道等运输市场不同经营门类分别制定考核指标，加强信用考核评价监督管理，积极引导第三方机构参与信用考核评价，逐步建立交通运输管理机构与社会信用评价机构相结合，具有监督、申诉和复核机制的综合考核评价体系。将各类交通运输违法行为列入失信记录。鼓励和支持各单位在采购交通运输服务、招标投标、人员招聘等方面优先选择信用考核等级高的交通运输企业和从业人员。对失信企业和从业人员，要加强监管和惩戒，逐步建立跨地区、跨行业信用奖惩联动机制。<br />
	<!--<br />-->
	　　电子商务领域信用建设。建立健全电子商务企业客户信用管理和交易信用评估制度，加强电子商务企业自身开发和销售信用产品的质量监督。推行电子商务主体身份标识制度，完善网店实名制。加强网店产品质量检查，严厉查处电子商务领域制假售假、传销活动、虚假广告、以次充好、服务违约等欺诈行为。打击内外勾结、伪造流量和商业信誉的行为，对失信主体建立行业限期禁入制度。促进电子商务信用信息与社会其他领域相关信息的交换和共享，推动电子商务与线下交易信用评价。完善电子商务信用服务保障制度，推动信用调查、信用评估、信用担保、信用保险、信用支付、商账管理等第三方信用服务和产品在电子商务中的推广应用。开展电子商务网站可信认证服务工作，推广应用网站可信标识，为电子商务用户识别假冒、钓鱼网站提供手段。<br />
	<!--<br />-->
	　　统计领域信用建设。开展企业诚信统计承诺活动，营造诚实报数光荣、失信造假可耻的良好风气。完善统计诚信评价标准体系。建立健全企业统计诚信评价制度和统计从业人员诚信档案。加强执法检查，严厉查处统计领域的弄虚作假行为，建立统计失信行为通报和公开曝光制度。加大对统计失信企业的联合惩戒力度。将统计失信企业名单档案及其违法违规信息纳入金融、工商等行业和部门信用信息系统，将统计信用记录与企业融资、政府补贴、工商注册登记等直接挂钩，切实强化对统计失信行为的惩戒和制约。<br />
	<!--<br />-->
	　　中介服务业信用建设。建立完善中介服务机构及其从业人员的信用记录和披露制度，并作为市场行政执法部门实施信用分类管理的重要依据。重点加强公证仲裁类、律师类、会计类、担保类、鉴证类、检验检测类、评估类、认证类、代理类、经纪类、职业介绍类、咨询类、交易类等机构信用分类管理，探索建立科学合理的评估指标体系、评估制度和工作机制。<br />
	<!--<br />-->
	　　会展、广告领域信用建设。推动展会主办机构诚信办展，践行诚信服务公约，建立信用档案和违法违规单位信息披露制度，推广信用服务和产品的应用。加强广告业诚信建设，建立健全广告业信用分类管理制度，打击各类虚假广告，突出广告制作、传播环节各参与者责任，完善广告活动主体失信惩戒机制和严重失信淘汰机制。<br />
	<!--<br />-->
	　　企业诚信管理制度建设。开展各行业企业诚信承诺活动，加大诚信企业示范宣传和典型失信案件曝光力度，引导企业增强社会责任感，在生产经营、财务管理和劳动用工管理等各环节中强化信用自律，改善商务信用生态环境。鼓励企业建立客户档案、开展客户诚信评价，将客户诚信交易记录纳入应收账款管理、信用销售授信额度计量，建立科学的企业信用管理流程，防范信用风险，提升企业综合竞争力。强化企业在发债、借款、担保等债权债务信用交易及生产经营活动中诚信履约。鼓励和支持有条件的企业设立信用管理师。鼓励企业建立内部职工诚信考核与评价制度。加强供水、供电、供热、燃气、电信、铁路、航空等关系人民群众日常生活行业企业的自身信用建设。<br />
	<!--<br />-->
	　　(三)全面推进社会诚信建设。<br />
	<!--<br />-->
	　　社会诚信是社会信用体系建设的基础，社会成员之间只有以诚相待、以信为本，才会形成和谐友爱的人际关系，才能促进社会文明进步，实现社会和谐稳定和长治久安。<br />
	<!--<br />-->
	　　医药卫生和计划生育领域信用建设。加强医疗卫生机构信用管理和行业诚信作风建设。树立大医精诚的价值理念，坚持仁心仁术的执业操守。培育诚信执业、诚信采购、诚信诊疗、诚信收费、诚信医保理念，坚持合理检查、合理用药、合理治疗、合理收费等诚信医疗服务准则，全面建立药品价格、医疗服务价格公示制度，开展诚信医院、诚信药店创建活动，制定医疗机构和执业医师、药师、护士等医务人员信用评价指标标准，推进医院评审评价和医师定期考核，开展医务人员医德综合评价，惩戒收受贿赂、过度诊疗等违法和失信行为，建立诚信医疗服务体系。加快完善药品安全领域信用制度，建立药品研发、生产和流通企业信用档案。积极开展以&ldquo;诚信至上，以质取胜&rdquo;为主题的药品安全诚信承诺活动，切实提高药品安全信用监管水平，严厉打击制假贩假行为，保障人民群众用药安全有效。加强人口计生领域信用建设，开展人口和计划生育信用信息共享工作。<br />
	<!--<br />-->
	　　社会保障领域信用建设。在救灾、救助、养老、社会保险、慈善、彩票等方面，建立全面的诚信制度，打击各类诈捐骗捐等失信行为。建立健全社会救助、保障性住房等民生政策实施中的申请、审核、退出等各环节的诚信制度，加强对申请相关民生政策的条件审核，强化对社会救助动态管理及保障房使用的监管，将失信和违规的个人纳入信用黑名单。构建居民家庭经济状况核对信息系统，建立和完善低收入家庭认定机制，确保社会救助、保障性住房等民生政策公平、公正和健康运行。建立健全社会保险诚信管理制度，加强社会保险经办管理，加强社会保险领域的劳动保障监督执法，规范参保缴费行为，加大对医保定点医院、定点药店、工伤保险协议医疗机构等社会保险协议服务机构及其工作人员、各类参保人员的违规、欺诈、骗保等行为的惩戒力度，防止和打击各种骗保行为。进一步完善社会保险基金管理制度，提高基金征收、管理、支付等各环节的透明度，推动社会保险诚信制度建设，规范参保缴费行为，确保社会保险基金的安全运行。<br />
	<!--<br />-->
	　　劳动用工领域信用建设。进一步落实和完善企业劳动保障守法诚信制度，制定重大劳动保障违法行为社会公示办法。建立用人单位拖欠工资违法行为公示制度，健全用人单位劳动保障诚信等级评价办法。规范用工行为，加强对劳动合同履行和仲裁的管理，推动企业积极开展和谐劳动关系创建活动。加强劳动保障监督执法，加大对违法行为的打击力度。加强人力资源市场诚信建设，规范职业中介行为，打击各种黑中介、黑用工等违法失信行为。<br />
	<!--<br />-->
	　　教育、科研领域信用建设。加强教师和科研人员诚信教育。开展教师诚信承诺活动，自觉接受广大学生、家长和社会各界的监督。发挥教师诚信执教、为人师表的影响作用。加强学生诚信教育，培养诚实守信良好习惯，为提高全民族诚信素质奠定基矗探索建立教育机构及其从业人员、教师和学生、科研机构和科技社团及科研人员的信用评价制度，将信用评价与考试招生、学籍管理、学历学位授予、科研项目立项、专业技术职务评聘、岗位聘用、评选表彰等挂钩，努力解决学历造假、论文抄袭、学术不端、考试招生作弊等问题。<br />
	<!--<br />-->
	　　文化、体育、旅游领域信用建设。依托全国文化市场技术监管与公共服务平台，建立健全娱乐、演出、艺术品、网络文化等领域文化企业主体、从业人员以及文化产品的信用信息数据库；依法制定文化市场诚信管理措施，加强文化市场动态监管。制定职业体育从业人员诚信从业准则，建立职业体育从业人员、职业体育俱乐部和中介企业信用等级的第三方评估制度，推进相关信用信息记录和信用评级在参加或举办职业体育赛事、职业体育准入、转会等方面广泛运用。制定旅游从业人员诚信服务准则，建立旅游业消费者意见反劳投诉记录与公开制度，建立旅行社、旅游景区和宾馆饭店信用等级第三方评估制度。<br />
	<!--<br />-->
	　　知识产权领域信用建设。建立健全知识产权诚信管理制度，出台知识产权保护信用评价办法。重点打击侵犯知识产权和制售假冒伪劣商品行为，将知识产权侵权行为信息纳入失信记录，强化对盗版侵权等知识产权侵权失信行为的联合惩戒，提升全社会的知识产权保护意识。开展知识产权服务机构信用建设，探索建立各类知识产权服务标准化体系和诚信评价制度。<br />
	<!--<br />-->
	　　环境保护和能源节约领域信用建设。推进国家环境监测、信息与统计能力建设，加强环保信用数据的采集和整理，实现环境保护工作业务协同和信息共享，完善环境信息公开目录。建立环境管理、监测信息公开制度。完善环评文件责任追究机制，建立环评机构及其从业人员、评估专家诚信档案数据库，强化对环评机构及其从业人员、评估专家的信用考核分类监管。建立企业对所排放污染物开展自行监测并公布污染物排放情况以及突发环境事件发生和处理情况制度。建立企业环境行为信用评价制度，定期发布评价结果，并组织开展动态分类管理，根据企业的信用等级予以相应的鼓励、警示或惩戒。完善企业环境行为信用信息共享机制，加强与银行、证券、保险、商务等部门的联动。加强国家能源利用数据统计、分析与信息上报能力建设。加强重点用能单位节能目标责任考核，定期公布考核结果，研究建立重点用能单位信用评价机制。强化对能源审计、节能评估和审查机构及其从业人员的信用评级和监管。研究开展节能服务公司信用评价工作，并逐步向全社会定期发布信用评级结果。加强对环资项目评审专家从业情况的信用考核管理。<br />
	<!--<br />-->
	　　社会组织诚信建设。依托法人单位信息资源库，加快完善社会组织登记管理信息。健全社会组织信息公开制度，引导社会组织提升运作的公开性和透明度，规范社会组织信息公开行为。把诚信建设内容纳入各类社会组织章程，强化社会组织诚信自律，提高社会组织公信力。发挥行业协会(商会)在行业信用建设中的作用，加强会员诚信宣传教育和培训。<br />
	<!--<br />-->
	　　自然人信用建设。突出自然人信用建设在社会信用体系建设中的基础性作用，依托国家人口信息资源库，建立完善自然人在经济社会活动中的信用记录，实现全国范围内自然人信用记录全覆盖。加强重点人群职业信用建设，建立公务员、企业法定代表人、律师、会计从业人员、注册会计师、统计从业人员、注册税务师、审计师、评估师、认证和检验检测从业人员、证券期货从业人员、上市公司高管人员、保险经纪人、医务人员、教师、科研人员、专利服务从业人员、项目经理、新闻媒体从业人员、导游、执业兽医等人员信用记录，推广使用职业信用报告，引导职业道德建设与行为规范。<br />
	<!--<br />-->
	　　互联网应用及服务领域信用建设。大力推进网络诚信建设，培育依法办网、诚信用网理念，逐步落实网络实名制，完善网络信用建设的法律保障，大力推进网络信用监管机制建设。建立网络信用评价体系，对互联网企业的服务经营行为、上网人员的网上行为进行信用评估，记录信用等级。建立涵盖互联网企业、上网个人的网络信用档案，积极推进建立网络信用信息与社会其他领域相关信用信息的交换共享机制，大力推动网络信用信息在社会各领域推广应用。建立网络信用黑名单制度，将实施网络欺诈、造谣传谣、侵害他人合法权益等严重网络失信行为的企业、个人列入黑名单，对列入黑名单的主体采取网上行为限制、行业禁入等措施，通报相关部门并进行公开曝光。<br />
	<!--<br />-->
	　　(四)大力推进司法公信建设。<br />
	<!--<br />-->
	　　司法公信是社会信用体系建设的重要内容，是树立司法权威的前提，是社会公平正义的底线。<br />
	<!--<br />-->
	　　法院公信建设。提升司法审判信息化水平，实现覆盖审判工作全过程的全国四级法院审判信息互联互通。推进强制执行案件信息公开，完善执行联动机制，提高生效法律文书执行率。发挥审判职能作用，鼓励诚信交易、倡导互信合作，制裁商业欺诈和恣意违约毁约等失信行为，引导诚实守信风尚。<br />
	<!--<br />-->
	　　检察公信建设。进一步深化检务公开，创新检务公开的手段和途径，广泛听取群众意见，保障人民群众对检察工作的知情权、参与权、表达权和监督权。继续推行&ldquo;阳光办案&rdquo;，严格管理制度，强化内外部监督，建立健全专项检查、同步监督、责任追究机制。充分发挥法律监督职能作用，加大查办和预防职务犯罪力度，促进诚信建设。完善行贿犯罪档案查询制度，规范和加强查询工作管理，建立健全行贿犯罪档案查询与应用的社会联动机制。<br />
	<!--<br />-->
	　　公共安全领域公信建设。全面推行&ldquo;阳光执法&rdquo;，依法及时公开执法办案的制度规范、程序时限等信息，对于办案进展等不宜向社会公开，但涉及特定权利义务、需要特定对象知悉的信息，应当告知特定对象，或者为特定对象提供查询服务。进一步加强人口信息同各地区、各部门信息资源的交换和共享，完善国家人口信息资源库建设。将公民交通安全违法情况纳入诚信档案，促进全社会成员提高交通安全意识。定期向社会公开火灾高危单位消防安全评估结果，并作为单位信用等级的重要参考依据。将社会单位遵守消防安全法律法规情况纳入诚信管理，强化社会单位消防安全主体责任。<br />
	<!--<br />-->
	　　司法行政系统公信建设。进一步提高监狱、戒毒场所、社区矫正机构管理的规范化、制度化水平，维护服刑人员、戒毒人员、社区矫正人员合法权益。大力推进司法行政信息公开，进一步规范和创新律师、公证、基层法律服务、法律援助、司法考试、司法鉴定等信息管理和披露手段，保障人民群众的知情权。<br />
	<!--<br />-->
	　　司法执法和从业人员信用建设。建立各级公安、司法行政等工作人员信用档案，依法依规将徇私枉法以及不作为等不良记录纳入档案，并作为考核评价和奖惩依据。推进律师、公证员、基层法律服务工作者、法律援助人员、司法鉴定人员等诚信规范执业。建立司法从业人员诚信承诺制度。<br />
	<!--<br />-->
	　　健全促进司法公信的制度基矗深化司法体制和工作机制改革，推进执法规范化建设，严密执法程序，坚持有法必依、违法必究和法律面前人人平等，提高司法工作的科学化、制度化和规范化水平。充分发挥人大、政协和社会公众对司法工作的监督作用，完善司法机关之间的相互监督制约机制，强化司法机关的内部监督，实现以监督促公平、促公正、促公信。<br />
	<!--<br />-->
	　　三、加强诚信教育与诚信文化建设<br />
	<!--<br />-->
	　　诚信教育与诚信文化建设是引领社会成员诚信自律、提升社会成员道德素养的重要途径，是社会主义核心价值体系建设的重要内容。<br />
	<!--<br />-->
	　　(一)普及诚信教育。<br />
	<!--<br />-->
	　　以建设社会主义核心价值体系、培育和践行社会主义核心价值观为根本，将诚信教育贯穿公民道德建设和精神文明创建全过程。推进公民道德建设工程，加强社会公德、职业道德、家庭美德和个人品德教育，传承中华传统美德，弘扬时代新风，在全社会形成&ldquo;以诚实守信为荣、以见利忘义为耻&rdquo;的良好风尚。<br />
	<!--<b/r />-->
	　　在各级各类教育和培训中进一步充实诚信教育内容。大力开展信用宣传普及教育进机关、进企业、进学校、进社区、进村屯、进家庭活动。<br />
	<!--<br />-->
	　　建好用好道德讲堂，倡导爱国、敬业、诚信、友善等价值理念和道德规范。开展群众道德评议活动，对诚信缺失、不讲信用现象进行分析评议，引导人们诚实守信、遵德守礼。<br />
	<!--<br />-->
	　　(二)加强诚信文化建设。<br />
	<!--<br />-->
	　　弘扬诚信文化。以社会成员为对象，以诚信宣传为手段，以诚信教育为载体，大力倡导诚信道德规范，弘扬中华民族积极向善、诚实守信的传统文化和现代市场经济的契约精神，形成崇尚诚信、践行诚信的社会风尚。<br />
	<!--<br />-->
	　　树立诚信典型。充分发挥电视、广播、报纸、网络等媒体的宣传引导作用，结合道德模范评淹各行业诚信创建活动，树立社会诚信典范，使社会成员学有榜样、赶有目标，使诚实守信成为全社会的自觉追求。<br />
	<!--<br />-->
	　　深入开展诚信主题活动。有步骤、有重点地组织开展&ldquo;诚信活动周&rdquo;、&ldquo;质量月&rdquo;、&ldquo;安全生产月&rdquo;、&ldquo;诚信兴商宣传月&rdquo;、&ldquo;35&rdquo;学雷锋活动日、&ldquo;315&rdquo;国际消费者权益保护日、&ldquo;614&rdquo;信用记录关爱日、&ldquo;124&rdquo;全国法制宣传日等公益活动，突出诚信主题，营造诚信和谐的社会氛围。<br />
	<!--<br />-->
	　　大力开展重点行业领域诚信问题专项治理。深入开展道德领域突出问题专项教育和治理活动，针对诚信缺失问题突出、诚信建设需求迫切的行业领域开展专项治理，坚决纠正以权谋私、造假欺诈、见利忘义、损人利己的歪风邪气，树立行业诚信风尚。<br />
	<!--<br />-->
	　　(三)加快信用专业人才培养。<br />
	<!--<br />-->
	　　加强信用管理学科专业建设。把信用管理列为国家经济体制改革与社会治理发展急需的新兴、重点学科，支持有条件的高校设置信用管理专业或开设相关课程，在研究生培养中开设信用管理研究方向。开展信用理论、信用管理、信用技术、信用标准、信用政策等方面研究。<br />
	<!--<br />-->
	　　加强信用管理职业培训与专业考评。建立健全信用管理职业培训与专业考评制度。推广信用管理职业资格培训，培养信用管理专业化队伍。促进和加强信用从业人员、信用管理人员的交流与培训，为社会信用体系建设提供人力资源支撑。<br />
	<!--<br />-->
	　　四、加快推进信用信息系统建设和应用<br />
	<!--<br />-->
	　　健全社会成员信用记录是社会信用体系建设的基本要求。发挥行业、地方、市场的力量和作用，加快推进信用信息系统建设，完善信用信息的记录、整合和应用，是形成守信激励和失信惩戒机制的基赐前提。<br />
	<!--<br />-->
	　　(一)行业信用信息系统建设。<br />
	<!--<br />-->
	　　加强重点领域信用记录建设。以工商、纳税、价格、进出口、安全生产、产品质量、环境保护、食品药品、医疗卫生、知识产权、流通服务、工程建设、电子商务、交通运输、合同履约、人力资源和社会保障、教育科研等领域为重点，完善行业信用记录和从业人员信用档案。<br />
	<!--<br />-->
	　　建立行业信用信息数据库。各部门要以数据标准化和应用标准化为原则，依托国家各项重大信息化工程，整合行业内的信用信息资源，实现信用记录的电子化存储，加快建设信用信息系统，加快推进行业间信用信息互联互通。各行业分别负责本行业信用信息的组织与发布。<br />
	<!--<br />-->
	　　(二)地方信用信息系统建设。<br />
	<!--<br/ />-->
	　　加快推进政务信用信息整合。各地区要对本地区各部门、各单位履行公共管理职能过程中产生的信用信息进行记录、完善、整合，形成统一的信用信息共享平台，为企业、个人和社会征信机构等查询政务信用信息提供便利。<br />
	<!--<br />-->
	　　加强地区内信用信息的应用。各地区要制定政务信用信息公开目录，形成信息公开的监督机制。大力推进本地区各部门、各单位政务信用信息的交换与共享，在公共管理中加强信用信息应用，提高履职效率。<br />
	<!--<br />-->
	　　(三)征信系统建设。<br />
	<!--<br />-->
	　　加快征信系统建设。征信机构开展征信业务，应建立以企事业单位及其他社会组织、个人为对象的征信系统，依法采集、整理、保存、加工企事业单位及其他社会组织、个人的信用信息，并采认理措施保障信用信息的准确性。各地区、各行业要支持征信机构建立征信系统。<br />
	<!--<br />-->
	　　对外提供专业化征信服务。征信机构要根据市场需求，对外提供专业化的征信服务，有序推进信用服务产品创新。建立健全并严格执行内部风险防范、避免利益冲突和保障信息安全的规章制度，依法向客户提供方便、快捷、高效的征信服务，进一步扩大信用报告在银行业、证券业、保险业及政府部门行政执法等多种领域中的应用。<br />
	<!--<br />-->
	　　(四)金融业统一征信平台建设。<br />
	<!--<br />-->
	　　完善金融信用信息基础数据库。继续推进金融信用信息基础数据库建设，提升数据质量，完善系统功能，加强系统安全运行管理，进一步扩大信用报告的覆盖范围，提升系统对外服务水平。<br />
	<!--<br/ />-->
	　　推动金融业统一征信平台建设。继续推动银行、证券、保险、外汇等金融管理部门之间信用信息系统的链接，推动金融业统一征信平台建设，推进金融监管部门信用信息的交换与共享。<br />
	<!--<br />-->
	　　(五)推进信用信息的交换与共享。<br />
	<!--<br />-->
	　　逐步推进政务信用信息的交换与共享。各地区、各行业要以需求为导向，在保护隐私、责任明确、数据及时准确的前提下，按照风险分散的原则，建立信用信息交换共享机制，统筹利用现有信用信息系统基础设施，依法推进各信用信息系统的互联互通和信用信息的交换共享，逐步形成覆盖全部信用主体、所有信用信息类别、全国所有区域的信用信息网络。各行业主管部门要对信用信息进行分类分级管理，确定查询权限，特殊查询需求特殊申请。<br />
	<!--<br />-->
	　　依法推进政务信用信息系统与征信系统间的信息交换与共享。发挥市场激励机制的作用，鼓励社会征信机构加强对已公开政务信用信息和非政务信用信息的整合，建立面向不同对象的征信服务产品体系，满足社会多层次、多样化和专业化的征信服务需求。<br />
	<!--<br />-->
	　　五、完善以奖惩制度为重点的社会信用体系运行机制<br />
	<!--<br />-->
	<!--　　<br />-->
	<!--<br />-->
	　　运行机制是保障社会信用体系各系统协调运行的制度基矗其中，守信激励和失信惩戒机制直接作用于各个社会主体信用行为，是社会信用体系运行的核心机制。<br />
	<!--<br />-->
	　　(一)构建守信激励和失信惩戒机制。<br />
	<!--<br />-->
	　　加强对守信主体的奖励和激励。加大对守信行为的表彰和宣传力度。按规定对诚信企业和模范个人给予表彰，通过新闻媒体广泛宣传，营造守信光荣的舆论氛围。发展改革、财政、金融、环境保护、住房城乡建设、交通运输、商务、工商、税务、质检、安全监管、海关、知识产权等部门，在市场监管和公共服务过程中，要深化信用信息和信用产品的应用，对诚实守信者实行优先办理、简化程序等&ldquo;绿色通道&rdquo;支持激励政策。<br />
	<!--<br />-->
	　　加强对失信主体的约束和惩戒。强化行政监管性约束和惩戒。在现有行政处罚措施的基础上，健全失信惩戒制度，建立各行业黑名单制度和市场退出机制。推动各级人民政府在市场监管和公共服务的市场准入、资质认定、行政审批、政策扶持等方面实施信用分类监管，结合监管对象的失信类别和程度，使失信者受到惩戒。逐步建立行政许可申请人信用承诺制度，并开展申请人信用审查，确保申请人在政府推荐的征信机构中有信用记录，配合征信机构开展信用信息采集工作。推动形成市场性约束和惩戒。制定信用基准性评价指标体系和评价方法，完善失信信息记录和披露制度，使失信者在市场交易中受到制约。推动形成行业性约束和惩戒。通过行业协会制定行业自律规则并监督会员遵守。对违规的失信者，按照情节轻重，对机构会员和个人会员实行警告、行业内通报批评、公开谴责等惩戒措施。推动形成社会性约束和惩戒。完善社会舆论监督机制，加强对失信行为的披露和曝光，发挥群众评议讨论、批评报道等作用，通过社会的道德谴责，形成社会震慑力，约束社会成员的失信行为。<br />
	<!--<br />-->
	　　建立失信行为有奖举报制度。切实落实对举报人的奖励，保护举报人的合法权益。<br />
	<!--<br />-->
	　　建立多部门、跨地区信用联合奖惩机制。通过信用信息交换共享，实现多部门、跨地区信用奖惩联动，使守信者处处受益、失信者寸步难行。<br />
	<!--<br/ />-->
	　　(二)建立健全信用法律法规和标准体系。<br />
	<!--<b/r />-->
	　　完善信用法律法规体系。推进信用立法工作，使信用信息征集、查询、应用、互联互通、信用信息安全和主体权益保护等有法可依。出台《征信业管理条例》相关配套制度和实施细则，建立异议处理、投诉办理和侵权责任追究制度。<br />
	<!--<br />-->
	　　推进行业、部门和地方信用制度建设。各地区、各部门分别根据本地区、相关行业信用体系建设的需要，制定地区或行业信用建设的规章制度，明确信用信息记录主体的责任，保证信用信息的客观、真实、准确和及时更新，完善信用信息共享公开制度，推动信用信息资源的有序开发利用。<br />
	<!--<br />-->
	　　建立信用信息分类管理制度。制定信用信息目录，明确信用信息分类，按照信用信息的属性，结合保护个人隐私和商业秘密，依法推进信用信息在采集、共享、使用、公开等环节的分类管理。加大对贩卖个人隐私和商业秘密行为的查处力度。<br />
	<!--<br />-->
	　　加快信用信息标准体系建设。制定全国统一的信用信息采集和分类管理标准，统一信用指标目录和建设规范。<br />
	<!--<br />-->
	　　建立统一社会信用代码制度。建立自然人、法人和其他组织统一社会信用代码制度。完善相关制度标准，推动在经济社会活动中广泛使用统一社会信用代码。<br />
	<!--<br />-->
	　　(三)培育和规范信用服务市常<br />
	<!--<br />-->
	　　发展各类信用服务机构。逐步建立公共信用服务机构和社会信用服务机构互为补充、信用信息基础服务和增值服务相辅相成的多层次、全方位的信用服务组织体系。<br />
	<!--<br />-->
	　　推进并规范信用评级行业发展。培育发展本土评级机构，增强我国评级机构的国际影响力。规范发展信用评级市场，提高信用评级行业的整体公信力。探索创新双评级、再评级制度。鼓励我国评级机构参与国际竞争和制定国际标准，加强与其他国家信用评级机构的协调和合作。<br />
	<!--<br />-->
	　　推动信用服务产品广泛运用。拓展信用服务产品应用范围，加大信用服务产品在社会治理和市场交易中的应用。鼓励信用服务产品开发和创新，推动信用保险、信用担保、商业保理、履约担保、信用管理咨询及培训等信用服务业务发展。<br />
	<!--<br />-->
	　　建立政务信用信息有序开放制度。明确政务信用信息的开放分类和基本目录，有序扩大政务信用信息对社会的开放，优化信用调查、信用评级和信用管理等行业的发展环境。<br />
	<!--<br />-->
	　　完善信用服务市场监管体制。根据信用服务市尝机构业务的不同特点，依法实施分类监管，完善监管制度，明确监管职责，切实维护市场秩序。推动制定信用服务相关法律制度，建立信用服务机构准入与退出机制，实现从业资格认定的公开透明，进一步完善信用服务业务规范，促进信用服务业健康发展。<br />
	<!--<br />-->
	　　推动信用服务机构完善法人治理。强化信用服务机构内部控制，完善约束机制，提升信用服务质量。<br />
	<!--<br />-->
	　　加强信用服务机构自身信用建设。信用服务机构要确立行为准则，加强规范管理，提高服务质量，坚持公正性和独立性，提升公信力。鼓励各类信用服务机构设立首席信用监督官，加强自身信用管理。<br />
	<!--<br />-->
	　　加强信用服务行业自律。推动建立信用服务行业自律组织，在组织内建立信用服务机构和从业人员基本行为准则和业务规范，强化自律约束，全面提升信用服务机构诚信水平。<br />
	<!--<br />-->
	　　(四)保护信用信息主体权益。<br />
	<!--<br />-->
	　　健全信用信息主体权益保护机制。充分发挥行政监管、行业自律和社会监督在信用信息主体权益保护中的作用，综合运用法律、经济和行政等手段，切实保护信用信息主体权益。加强对信用信息主体的引导教育，不断增强其维护自身合法权益的意识。<br />
	<!--<br />-->
	　　建立自我纠错、主动自新的社会鼓励与关爱机制。以建立针对未成年人失信行为的教育机制为重点，通过对已悔过改正旧有轻微失信行为的社会成员予以适当保护，形成守信正向激励机制。<br />
	<!--<br />-->
	　　建立信用信息侵权责任追究机制。制定信用信息异议处理、投诉办理、诉讼管理制度及操作细则。进一步加大执法力度，对信用服务机构泄露国家秘密、商业秘密和侵犯个人隐私等违法行为，依法予以严厉处罚。通过各类媒体披露各种侵害信息主体权益的行为，强化社会监督作用。<br />
	<!--<b/r />-->
	　　(五)强化信用信息安全管理。<br />
	<!--<br />-->
	　　健全信用信息安全管理体制。完善信用信息保护和网络信任体系，建立健全信用信息安全监控体系。加大信用信息安全监督检查力度，开展信用信息安全风险评估，实行信用信息安全等级保护。开展信用信息系统安全认证，加强信用信息服务系统安全管理。建立和完善信用信息安全应急处理机制。加强信用信息安全基础设施建设。<br />
	<!--<br />-->
	　　加强信用服务机构信用信息安全内部管理。强化信用服务机构信息安全防护能力，加大安全保障、技术研发和资金投入，高起点、高标准建设信用信息安全保障系统。依法制定和实施信用信息采集、整理、加工、保存、使用等方面的规章制度。<br />
	<!--<br />-->
	　　六、建立实施支撑体系<br />
	<!--<br />-->
	　　(一)强化责任落实。<br />
	<!--<br />-->
	　　各地区、各部门要统一思想，按照本规划纲要总体要求，成立规划纲要推进小组，根据职责分工和工作实际，制定具体落实方案。<br />
	<!--<br />-->
	　　各地区、各部门要定期对本地区、相关行业社会信用体系建设情况进行总结和评估，及时发现问题并提出改进措施。<br />
	<!--<br />-->
	　　对社会信用体系建设成效突出的地区、部门和单位，按规定予以表彰。对推进不力、失信现象多发地区、部门和单位的负责人，按规定实施行政问责。<br />
	<!--<br />-->
	　　(二)加大政策支持。<br />
	<!--<br />-->
	　　各级人民政府要根据社会信用体系建设需要，将应由政府负担的经费纳入财政预算予以保障。加大对信用基础设施建设、重点领域创新示范工程等方面的资金支持。<br />
	<!--<br />-->
	　　鼓励各地区、各部门结合规划纲要部署和自身工作实际，在社会信用体系建设创新示范领域先行先试，并在政府投资、融资安排等方面给予支持。<br />
	<!--<br />-->
	　　(三)实施专项工程。<br />
	<!--</br />-->
	　　政务信息公开工程。深入贯彻实施《中华人民共和国政府信息公开条例》，按照主动公开、依申请公开进行分类管理，切实加大政务信息公开力度，树立公开、透明的政府形象。<br />
	<!--<br />-->
	　　农村信用体系建设工程。为农户、农尝农民合作社、休闲农业和农产品生产、加工企业等农村社会成员建立信用档案，夯实农村信用体系建设的基矗开展信用户、信用村、信用乡(镇)创建活动，深入推进青年信用示范户工作，发挥典型示范作用，使农民在参与中受到教育，得到实惠，在实践中提高信用意识。推进农产品生产、加工、流通企业和休闲农业等涉农企业信用建设。建立健全农民信用联保制度，推进和发展农业保险，完善农村信用担保体系。<br />
	<!--<br />-->
	　　小微企业信用体系建设工程。建立健全适合小微企业特点的信用记录和评价体系，完善小微企业信用信息查询、共享服务网络及区域性小微企业信用记录。引导各类信用服务机构为小微企业提供信用服务，创新小微企业集合信用服务方式，鼓励开展形式多样的小微企业诚信宣传和培训活动，为小微企业便利融资和健康发展营造良好的信用环境。<br />
	<!--<br />-->
	　　(四)推动创新示范。<br />
	<!--<br />-->
	　　地方信用建设综合示范。示范地区率先对本地区各部门、各单位的信用信息进行整合，形成统一的信用信息共享平台，依法向社会有序开放。示范地区各部门在开展经济社会管理和提供公共服务过程中，强化使用信用信息和信用产品，并作为政府管理和服务的必备要件。建立健全社会信用奖惩联动机制，使守信者得到激励和奖励，失信者受到制约和惩戒。对违法违规等典型失信行为予以公开，对严重失信行为加大打击力度。探索建立地方政府信用评价标准和方法，在发行地方政府债券等符合法律法规规定的信用融资活动中试行开展地方政府综合信用评价。<br />
	<!--<br />-->
	　　区域信用建设合作示范。探索建立区域信用联动机制，开展区域信用体系建设创新示范，推进信用信息交换共享，实现跨地区信用奖惩联动，优化区域信用环境。<br />
	<!--<br />-->
	　　重点领域和行业信用信息应用示范。在食品药品安全、环境保护、安全生产、产品质量、工程建设、电子商务、证券期货、融资担保、政府采购、招标投标等领域，试点推行信用报告制度。<br />
	<!--<b/r />-->
	　　(五)健全组织保障。<br />
	<!--</br />-->
	　　完善组织协调机制。完善社会信用体系建设部际联席会议制度，充分发挥其统筹协调作用，加强对各地区、各部门社会信用体系建设工作的指导、督促和检查。健全组织机构，各地区、各部门要设立专门机构负责推动社会信用体系建设。成立全国性信用协会，加强行业自律，充分发挥各类社会组织在推进社会信用体系建设中的作用。<br />
	<!--<br />-->
	　　建立地方政府推进机制。地方各级人民政府要将社会信用体系建设纳入重要工作日程，推进政务诚信、商务诚信、社会诚信和司法公信建设，加强督查，强化考核，把社会信用体系建设工作作为目标责任考核和政绩考核的重要内容。<br />
	<!--<br />-->
	　　建立工作通报和协调制度。社会信用体系建设部际联席会议定期召开工作协调会议，通报工作进展情况，及时研究解决社会信用体系建设中的重大问题。</p> </div>
		</div>
		<!--<div class="p1">还可以将社会主体按信用状况分成若干等级</div>-->

		<div class="hr2"></div>
	</div>
</template>

<script>
	export default {
		props: ['newsDetail'],

	};
</script>

<style lang="less" scoped>
	.detail_1 {
		.hr1 {
			margin-top: 40px;
			margin-bottom: 10px;
			border: 0;
			border-top: 1px solid #eee;
		}
		.header {
			font-size: 18px;
			text-align: center;
			margin-bottom: 5px;
			color: #b62a31;
		}
		.nav {
			width: 360px;
			color: #888;
			margin: 0 auto;
			font-size: 12px;
			.left {
				float: left;
				padding-left: 15px;
			}
			.right {
				float: right;
				padding-right: 15px;
			}
		}
		.img {
			/*height: 430px;*/
			text-align: center;
		}
		.p1 {
			font-size: 16px;
			text-indent: 2em;
			padding-bottom: 5px;
			line-height: 1.75;
			color: #888;
		}
		.p2 {
			font-size: 16px;
			text-align: center;
		}
		.hr2 {
			margin-top: 20px;
			margin-bottom: 10px;
			border: 0;
			border-top: 1px solid #eee;
		}
	}
</style>


